*{
    margin: 0;
    padding: 0;
}
.list li{
    margin: 5px;
    padding: 20px;
    text-align: center;
    background:rgba(214, 224, 213, 0.637) ; 
}
.close{
    position: fixed;
    top:10px;
    right: 10px;
    width:40px;
    background: black;
    padding: 10px;
    color: white;
    font-weight: 900;
    border:none;
    border-radius: 50%;
}
