}
 input, label, textarea, .edu p, .btngroup{
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 7px;
    width: 90vw;
}

 textarea{
    min-height: 100px;
}
 label{
    background: chartreuse;
}

.edu p{
    background: rgb(229, 255, 0);
}


.btngroup button{
    
      margin-left: 10px;
      margin-top: 10px;
      border-radius: 50%;
      width:30px;
      height:30px;
      border:1px solid chartreuse;
}
.resume h1{
    text-align: center;
